  body.layout-boxed {
    background: #d5dddc;
  }

  body.layout-boxed.dark-theme {
    background: #1d1d2a;
  }

  .layout-boxed {
    .page {
      width: 80%;
      margin: 0 auto;
      background: #edf3f2;
      position: relative;
      display: -ms-flexbox;
      display: -webkit-box;
      display: flex;
      -ms-flex-direction: column;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      flex-direction: column;
      -ms-flex-pack: center;
      -webkit-box-pack: center;
      -webkit-box-shadow: rgba(104, 113, 123, 0.3) 0px 5px 0px 0px,
        rgba(104, 113, 123, 0.3) 0px 5px 25px 0px;
      box-shadow: rgba(104, 113, 123, 0.3) 0px 5px 0px 0px,
        rgba(104, 113, 123, 0.3) 0px 5px 25px 0px;
    }

    .main-header {
      width: 80% !important;
      margin: 0 auto;
    }

    .main-header.hor-header {
      width: 100% !important;
      margin: 0 auto;
    }

    .app-sidebar {
      left: auto !important;
    }

    .main-sidebar-header {
      left: auto !important;
      right: auto !important;
    }

    h3.number-font {
      font-size: 1.8rem;
    }

    .sticky-pin .horizontal-main.hor-menu {
      width: 1300px !important;
    }
    .main-header-left .form-control {
      width: 300px !important;
    }
    &.scrollable-layout {
      .main-header {
        width: 100% !important;
      }
    }
  }

  .dark-theme.layout-boxed .page {
    background: #1c2030;
    -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px,
      rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px,
      rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
  }

  .horizontal.layout-boxed {
    .sticky-pin {
      max-width: 80%;
    }
  }

  @media (min-width: 768px) {
